.chat{
    display: flex;
    flex-direction: column;
    flex: 0.65;
}
.chat__headerRight{}
.chat__headerInfo{
    flex: 1;
    padding-left: 20px;
}
.chat__header{
  padding: 20px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid lightgray;   
}
.chat__headerInfo > h3{
    margin-bottom: 3px;
    font-weight: 500;
}
.chat__headerInfo > p{
   color: gray;
}
.chat__body{
    flex: 1;
    background-image: url('./wa1.png');
    background-repeat: repeat;
    background-position: center;
    padding: 30px;
    overflow-y: scroll;
}
.chat__body::-webkit-scrollbar{
    width: 10px;
}
.chat__message{
    position: relative;
    font-size: 16px;
    padding: 10px;
    width: fit-content;
    background-color: #fff;
    border-radius: 10px;
    margin-bottom: 30px;
}
.chat__timestamp{
    margin-left: 10px;
    font-size: xx-small;
}
.chat__name{
    position: absolute;
    top: -15px;
    font-weight: 800;
    font-size: xx-small;
}
.chat__reciever{
    margin-left: auto;
    background-color: #dcf8c6;
}
.chat__footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 62px;
    border-top: 1px solid lightgray;
}
.chat__footer > form{
    flex: 1;
    display: flex;
}
.chat__footer > form > input{
    flex: 1;
    border-radius: 30px;
    padding: 10px;
    border: none;
}
.chat__footer > form > button{
/* display: none; */
outline: none;
border: none;
}
.chat__footer > .MuiSvgIcon-root{
    padding: 10px;
    color: gray;
}
.btnn{
    border: none;
    outline: none;
}